<template>
  <div class="content">
    <h3 class="title fg-darkCyan">Datepicker </h3>
    <div style="width: 200px;">
      <datepicker :moment.sync="moment"></datepicker>
    </div>
    <p class="fg-gray">Html</p>
<pre v-prettify class="prettyprint linenums"><code v-pre>&lt;!--datepicker, datepicker()--&gt;
&lt;div style="width: 200px;"&gt;
&lt;datepicker :moment.sync="moment"&gt;&lt;/datepicker&gt;
&lt;/div&gt;
</code></pre>
    <p class="fg-gray">js</p>
<pre v-prettify class="prettyprint linenums"><code v-pre> data() {
  return {
    // js
    moment: new Date().getTime()
  }
}
</code></pre>
      <p class="fg-gray">Props </p>
      <table class="table striped hovered bordered border">
        <thead>
          <th>Prop</th>
          <th>Type</th>
          <th>Required</th>
          <th>Default</th>
          <th>Description</th>
        </thead>
        <tbody>
          <tr>
            <td>moment</td>
            <td>Number</td>
            <td>True</td>
            <td></td>
            <td>()</td>
          </tr>
        </tbody>
      </table>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        moment: new Date().getTime()
      }
    },
    watch: {
      moment() {
        const date = new Date(this.moment)
        const msg = `${date.getFullYear()}${date.getMonth() + 1}${date.getDate()}`
        this.$message({
          caption: '',
          content: msg,
          type: 'info'
        })
      }
    }
  }
</script>
<style lang="scss">
  
</style>



