<template>
  <div class="content">
    <h3 class="title fg-darkCyan">Select </h3>
    <vi-select :options="options" :selected.sync="selected" :width="220"></vi-select>
    <p class="fg-gray">Html</p>
<pre v-prettify class="prettyprint linenums"><code v-pre>&lt;!--vi-select--&gt;
&lt;vi-select :options="options" :selected.sync="selected" :width="220"&gt;&lt;/vi-select&gt;
</code></pre>
<p class="fg-gray">js</p>
<pre v-prettify class="prettyprint linenums"><code v-pre>export default {
    data() {
      return {
        options: [
          { value: 1, text: '' },
          { value: 2, text: '' },
          { value: 3, text: '' },
          { value: 4, text: '' },
          { value: 5, text: '' },
          { value: 6, text: '' },
          { value: 7, text: '' },
          { value: 8, text: '' },
          { value: 9, text: '' },
          { value: 10, text: '' }
        ],
        selected: { value: 3, text: '' }
      }
    },
    watch: {
      selected() {
        this.$message({
          caption: '',
          content: `${this.selected.text}`,
          type: 'alert'
        })
      }
    }
  }
</code></pre>
  <br>
 <p class="fg-gray">Props </p>
  <table class="table striped hovered bordered border">
    <thead>
      <th>Prop</th>
      <th>Type</th>
      <th>Required</th>
      <th>Default</th>
      <th>Description</th>
    </thead>
    <tbody>
      <tr>
        <td>options</td>
        <td>Array</td>
        <td>True</td>
        <td></td>
        <td>{value: x, text: xxx}</td>
      </tr>
      <tr>
        <td>selected.sync</td>
        <td>Object</td>
        <td>False</td>
        <td></td>
        <td>(options),</td>
      </tr>
      <tr>
        <td>width</td>
        <td>Number</td>
        <td>False</td>
        <td>200</td>
        <td></td>
      </tr>
    </tbody>
  </table>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        options: [
          { value: 1, text: '' },
          { value: 2, text: '' },
          { value: 3, text: '' },
          { value: 4, text: '' },
          { value: 5, text: '' },
          { value: 6, text: '' },
          { value: 7, text: '' },
          { value: 8, text: '' },
          { value: 9, text: '' },
          { value: 10, text: '' }
        ],
        selected: { value: 3, text: '' }
      }
    },
    watch: {
      selected() {
        this.$message({
          caption: '',
          content: `${this.selected.text}`,
          type: 'alert'
        })
      }
    }
  }
</script>
<style lang="scss">  
</style>



