<template>
  <div class="content">
    <h3 class="title fg-darkCyan">Upload </h3>
    <div style="width: 220px;">
      <upload :name="'img'" @fileChanged="fileChanged"></upload>
    </div>

    <p class="fg-gray">HTML</p>
<pre v-prettify class="prettyprint linenums"><code v-pre>&lt;div style="width: 220px;"&gt;
  &lt;!----&gt;
  &lt;upload :name="'img'" @fileChanged="fileChanged"&gt;&lt;/upload&gt;
&lt;/div&gt;
</code></pre>

      <p class="fg-gray">Script</p>
<pre v-prettify class="prettyprint linenums"><code v-pre>data() {
  return {}
},
methods: {
  fileChanged(file) {
    const msg = file ? `${file.name}` : ''
    const msgType = file ? 'success' : 'warning'
    this.$message({
      content: msg,
      type: msgType
    })
  }
}
</code></pre>
      <br>
      <p class="fg-gray">Props </p>
      <table class="table striped hovered bordered border">
        <thead>
          <th>Prop</th>
          <th>Type</th>
          <th>Required</th>
          <th>Default</th>
          <th>Description</th>
        </thead>
        <tbody>
          <tr>
            <td>name</td>
            <td>String</td>
            <td>False</td>
            <td></td>
            <td>input[type="file"]name, form</td>
          </tr>
        </tbody>
      </table>

      <br>
    <p class="fg-gray">Binding attributes </p>
    <table class="table striped hovered bordered border">
      <thead>
        <th>Attribute Name</th>
        <th>Type</th>
        <th>Argument</th>
        <th>Description</th>
      </thead>
      <tbody>
        <tr>
          <td>fileChanged</td>
          <td>Function</td>
          <td>file()</td>
          <td></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
  export default {
    data() {
      return {}
    },
    methods: {
      fileChanged(file) {
        const msg = file ? `${file.name}` : ''
        const msgType = file ? 'success' : 'warning'
        this.$message({
          content: msg,
          type: msgType
        })
      }
    }
  }
</script>



