<template>
  <div class="content">
    <h3 class="title fg-darkCyan">Confirm </h3>
    <button @click="confirm()" class="button">Confirm</button>
    <br>
    <p>Script</p>
    <pre v-prettify class="prettyprint linenums"><code v-pre>this.$confirm({
        caption: 'confirm Title',   // Title
        content: 'confirm Content',       // 
    }).then(() => {    // Promise, 
      // 
    }).catch(() => {
      // 
    })</code></pre>
  </div>
</template>
<script>
  export default {
    data() {
      return {}
    },
    methods: {
      confirm() {
        this.$confirm('').then(() => {
          this.$message({
            caption: '',
            content: '',
            type: 'success'
          })
        }).catch(() => {
          this.$message({
            caption: '',
            content: '',
            type: 'alert'
          })
        })
      }
    }
  }
</script>
<style lang="scss">
  
</style>



