<template>
  <div class="content">
    <h3 class="title fg-darkCyan">Pagination </h3>
    <pagination :total="120" :next="go" :show-total="true"></pagination>
    <br>
    <p class="fg-gray"></p>
<pre v-prettify class="prettyprint linenums"><code v-pre>&lt;div&gt;
  &lt;!----&gt;
  &lt;pagination :total="120" :next="go" :show-total="true"&gt;&lt;/pagination&gt;
&lt;/div&gt;
</code></pre>
    <p class="fg-gray">Props </p>
    <table class="table striped hovered bordered border">
      <thead>
        <th>Prop</th>
        <th>Type</th>
        <th>Required</th>
        <th>Default</th>
        <th>Description</th>
      </thead>
      <tbody>
        <tr>
          <td>pn</td>
          <td>Number</td>
          <td>False</td>
          <td>1</td>
          <td>(1)</td>
        </tr>
        <tr>
          <td>total</td>
          <td>Number</td>
          <td>True</td>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>size</td>
          <td>Number</td>
          <td>False</td>
          <td>10</td>
          <td>()</td>
        </tr>
        <tr>
          <td>next</td>
          <td>Function</td>
          <td>False</td>
          <td>Function</td>
          <td></td>
        </tr>
        <tr>
          <td>show-total</td>
          <td>Boolean</td>
          <td>False</td>
          <td>false</td>
          <td></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        text: '1'
      }
    },
    methods: {
      go(n) {
        this.$message(`${n}`)
      }
    }
  }
</script>



